.ContactCardInfo{
	text-align: center;	
	margin: 0px;
	box-sizing: border-box;
	background: white;
	height: 100%;
}

/*Includes Profile Pic, Name, and position title of the person*/
.ContactCardInfo .contactOverview{
	padding-top: 5%;
	/* position: sticky; */
	top: 0;
	background-color: #d6d6d6;
	width: 100%;
    margin: auto;
    
	/*Shadows*/
	box-shadow: 0px 0px 40px 0px rgba(0,0,0,0.1);
	-webkit-box-shadow: 0px 0px 40px 0px rgba(0,0,0,0.1);
	-moz-box-shadow: 0px 0px 40px 0px rgba(0,0,0,0.1);
}

.ContactCardInfo .contactOverview .backButton{
	position: absolute;
	top: 8px;
	left: 6px;
	margin: auto;
}

/*Name of employee */
.ContactCardInfo .contactOverview .employeeName{
	font-family: 'Century Gothic', 'Calibri', 'Arial';
	font-size: 150%;
	font-weight: 400;
}
/*Employee title*/
.ContactCardInfo .contactOverview .employeeTitle{
	font-size: 100%;
	padding-top: 2%;
	padding-bottom: 2.5%;
}

.ContactCardInfo .contactOverview img{
	height: 35%;
	width: 35%;
	max-width: 200px;
	max-height: 200px;
}

/*Container for Detailed Info section*/
.ContactCardInfo .detailedInfo{
	display: inline-block;
	-webkit-justify-content: center;
	        justify-content: center;
	padding-top: 10%;
	padding-bottom: 5%;
	width: 95%;
}

/*All children of above container*/
.ContactCardInfo .detailedInfo .phoneInfo{
	margin: auto;
	padding: 5px;	
}


.ContactCardInfo .detailedInfo .emailInfo{
	margin: auto;
	padding: 5px;
}


.ContactCardInfo .detailedInfo .locationInfo{
	margin: auto;
	padding: 5px;
}

 /*All <a href> tags inside detailed info section*/
.ContactCardInfo .detailedInfo * a{
	text-decoration: none;
  	color: black;
}

/*All h2 tags inside detailedInfo flexbox*/
.ContactCardInfo .detailedInfo * h2{
	font-size: 125%;
	font-weight: 300;
	padding: auto;
	font-family: "Century Gothic", "Calibri", "Arial";
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.ContactCardInfo .detailedInfo * img{
	width: 50px;
	height: 50px;
}

.ContactCardInfo .emergencyContactInfo{
	display: inline-block;
	-webkit-justify-content: center;
	        justify-content: center;
	text-decoration: none;
  	color: inherit;
	
	width: 95%;
	padding-bottom: 10%;
	padding-top: 5%;
}

.ContactCardInfo .emergencyContactInfo .emergencyContactProfilePic{
	width: 100%;
	height: 100%;
}

.ContactCard{
	margin: auto;
	padding: auto;
}
.ContactSummary {
  
  margin: 15px;
  background: #fff;
  /* vertical-align: center; */
  border: solid 1px #c4c4c4;
  border-radius: 8px;

  /*Shadows*/
  /* box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.1);
  -webkit-box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.1);
  -moz-box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.1); */

  transition: -webkit-transform .2s ease;

  transition: transform .2s ease;

  transition: transform .2s ease, -webkit-transform .2s ease;
}

/*Make the element pop-out of the screen upon mouse hover*/
/* .ContactSummary:hover {
  transform: scale(1.02);
} */

/*Username of Employee*/
.ContactSummary .userName{
  width: 65%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  
}

/* Position of Employee (secondary text of ListItem)*/
.ContactSummary .userPosition{
  font-size: 90%;
  color: rgba(0, 0, 0, 0.7);

  width: 60%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  
}

/*Check in button*/
.ContactSummary .btnCheckIn {
  background-color: #4CAF50; /* Green */
  color: white;

  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 8px;
  padding-right: 8px;
  margin-right: 10px;

  width: 90px;
  height: 45px;

  border: none;
  border-radius: 4px;

  font-size: 85%;
}

/*Undo Check-In button*/
.ContactSummary .btnUndoCheckIn {
  background-color: #e83c3c; /* Red */
  color: white;

  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 8px;
  padding-right: 8px;
  margin-right: 10px;

  width: 90px;
  height: 45px;

  border: none;
  border-radius: 4px;

  font-size: 85%;
}

